@import "config/index";
@import "utilities/index";
@import "base/index";
@import "global";

@import "components/page-footer";
@import "components/page-frame";
@import "components/page-header";

@import "code/index";

/* hiding elements/icons.css until mixmatch icons are resolve */
@import "elements/controls";
@import "elements/buttons";
@import "elements/messages";

@import "bilingual.css";
@import "impressum";
@import "disclaimer";

#main
{
    width: auto;
    margin-left: 0;
    margin-right: 0;
}

.v2 h1{
    color: $text;    
    font-family: segoe-ui_normal, 'Segoe UI', Segoe, 'Segoe WP', 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 1.25rem;    
    font-weight: 600;    
    line-height: 1.2;
    margin: 6px 12px 0 0px;    
    padding-left:18px;
}

html.theme_night .v2 h1{
    color:#ccc;
}

.adHolder {
    margin:12px 0 0 12px;
}

.adHolder h3 {
    color: $text-teal;
    font-size: 0.875rem;
    margin:0;
}

.adHolder p {
    color: $blue;
    font-size: 0.875rem;
    margin:0;
}

.adHolder:hover p, .adHolder:active p {
    text-decoration: underline;
}

html[dir='rtl'] .adHolder {
    margin:12px 12px 0 0;
}

@media only screen and (min-width: $tablet) {
    .adHolder {
        position: absolute;
        top: 0;
        right: 16px;
    }

    .adHolder h3 {
        font-size: 1.18rem;
    }

    .adHolder p {
        font-size: 1rem;
    }

    html[dir='rtl'] .adHolder {
        left: 16px;
        right: auto;
    }
}

.pivotTabs{
    display:none;
}

.pivotTabs ul {
    margin: 24px 0 40px;
    border-bottom: 1px solid #ccdcf3;
}

html.theme_night .pivotTabs ul{
    border-bottom-color: #666;
}

.pivotTabs li { 
    display: inline-block;
    font-family: segoe-ui_light, 'Segoe UI Light', 'Segoe WP', 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 2rem;
    font-weight: 100;
    line-height: 1.68;
    list-style: none;
    margin: 0 4px -1px 0;
    padding: 0 40px;  
    border-left: 1px solid transparent;
	border-right: 1px solid transparent;
	border-bottom: 1px solid transparent;
	border-top: 5px solid transparent;  
}

.pivotTabs li:hover, .pivotTabs li:active {
	background-color: rgba(0, 80, 197, .07);
}

.pivotTabs li.selected {
	border-left: 1px solid #ccdcf3;
	border-right: 1px solid #ccdcf3;
	border-bottom: 1px solid #fff;
	border-top: 5px solid $blue;
}

html.theme_night .pivotTabs li.selected {
	border-bottom-color: #222;
}


.pivotTabs li.selected a {
    color:#222;
}

.pivotTabs a, .pivotTabs a:visited, .pivotTabs a:hover, .pivotTabs a:active {
    color: $text-subtle;
    text-decoration:none;
}

html.theme_night .pivotTabs a, html.theme_night .pivotTabs a:visited, html.theme_night .pivotTabs a:hover, html.theme_night .pivotTabs a:active {
    color:#ccc;
}

.singlePivot {
    display: none !important;
}

.tabless {
    margin-top:48px;
}

.pivotSelector{
    background:#ccc url(../images/ham_pivotSelector.svg) no-repeat;
    background-size:24px 24px;
    background-position: 12px 6px;
    height: 32px;
    margin:24px 0 12px 0;
    padding-left:46px;
    padding-top:4px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor:pointer;
}

html.theme_night .pivotSelector {
    color:#ccc;
}

html[dir='rtl'] .pivotSelector{
    padding-left:0;
    padding-right:48px;
    background-position: calc(100% - 12px) 6px;
}

@media only screen and (min-width: $tablet) {
    .v2 h1 {
        font-size: 1.625rem;
        padding-left:14px;
    }

    .pivotTabs{
        display:block;
    }

    .pivotSelector{
        display:none;
    }
}

.pivotMenu .pivots{
    margin-top:-12px;
    background-color: #e6e6e6;
    padding:24px;
}

.pivotMenu .pivots a{
    color:$text !important;
}

.pivots > li{
    display:none;
}

.pivotMenu .pivots > li{
    display:block !important;
}

.pivotItem {
    list-style-type: none;
}

.pivotItem > a{
    display:none;    
}

.pivotMenu .pivotItem > a{
    display:block;    
}

.pivotMenu .pivotItem > ul{
    margin-top:6px;    
}

.panelItem{
    list-style-type: none;
}

.panelItem > a {
	font-size: 1.5rem;
    line-height: 1.33;
	display: none;
	padding: 6px 0 8px 12px;
    margin: 0 0 12px 0;
    color: $text-subtle;
    cursor:text;
}

html.theme_night .panelItem > a {
    color:#ccc;
}

html[dir='rtl'] .panelItem > a {
	padding: 6px 12px 8px 0;
}

.pivotMenu .panelItem > a {
	display: block;
    font-size: 0.875rem !important;
    cursor:pointer !important;
}

.panelItem > a.selected {
    text-align: center;
    display: block;
}

.pivotMenu .panelItem > a.selected {
    text-align: left;
    font-weight:bold;
    background-color:#999;
}

html[dir='rtl'] .pivotMenu .panelItem > a.selected {
    text-align: right;
}

.singlePanelNavItem
{
    display:none !important;
}

.singlePanelContent
{
    float:none !important;
    margin:0 auto !important;
}

.featuredContent
{
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    float:none !important;
    margin:0 auto;
}

@media only screen and (min-width: $tablet) {
    .featuredContent
    {
        margin:2px auto 10px;
    }
}

.panelItem > ul {
    display: none;
}

.pivotMenu .panelContent {
    display: none !important;
}

.panelContent > li {    
    list-style-type: none;
    -webkit-flex: 0 1 100%;
    -ms-flex: 0 1 100%;
    flex: 0 1 100%;
}

.panelContent > li.fullSpan {    
    -webkit-flex: 0 1 100% !important;
    -ms-flex: 0 1 100% !important;
    flex: 0 1 100% !important;
}

@media only screen and (min-width: $tablet) {
    .panelItem > a {
        font-size: 0.9375rem;
        display: inline-block;
        width: 228px;
        margin: 0 0 6px 0;
        cursor:pointer;
    }

    .panelItem > a:hover, .panelItem > a:active {
        color: $blue;
        background-color: rgba(0, 80, 197, 0.07);
    }

    .panelItem > a.selected {
        text-align: left;
        background-color: $blue;
        color: #fff;
        display: inline-block;
    }

    html[dir='rtl'] .panelItem > a.selected {
        text-align: right;
    }

    .panelContent {
        float:right;
        width: calc( 100% - 276px);
    }

    .panelContent > li {    
        -webkit-flex: 0 1 50%;
        -ms-flex: 0 1 50%;
        flex: 0 1 50%;
    }

    html[dir='rtl'] .panelContent {
        float:left;
    }
}

@media only screen and (min-width: $desktop) {
    .panelContent > li {    
        -webkit-flex: 0 1 33%;
        -ms-flex: 0 1 33%;
        flex: 0 1 33%;
    }
}

.panelContent {
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.panelContent .container {
    padding: 0;
}

.panelContent h1 {
    font-weight: normal;
    margin-bottom:36px;
    margin-top:48px;
}

.panelContent > li:first-child h1 {
    margin-top:6px;
}

.panelContent .container p {
    padding: 0 12px;
}

.panelContent .container>p:first-child {
    margin-top:0;
    padding-top:0;
}

@media only screen and (max-width: $tablet-minus1) {
    .panelContent {
        margin-top:0 !important;
    }
}

.panelContent .intro{
    font-size: 1.125rem;
    line-height: 1.7;
    margin:0 0 16px 0;
    max-width: 900px;
}

@import "components/hubCards";

/* site home below */

.siteHome h1{
    display:none;
}

.siteHome .intro{
    color:#6e6e6e;
    line-height: 1.375;
    margin:24px auto 36px;
    max-width:562px; 
    padding:0 16px;   
}

@media only screen and (min-width: $tablet) {
    .siteHome .intro{
        margin:24px auto 84px;
    }
}

.siteHome .cardsX{
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
    width:100%;
}

html.theme_night .siteHome .cardsX .card{
    background-color: #fff;
}

.siteHome .ready .windows:hover, .siteHome .ready .windows:active, .siteHome .ready .sql:hover, .siteHome .ready .sql:active {
	background-color: #0078d7;
}

.siteHome .ready .azure:hover, .siteHome .ready .azure:active, .siteHome .ready .xamarin:hover, .siteHome .ready .xamarin:active {
	background-color: #3498db;
}

.siteHome .ready .visualstudio:hover, .siteHome .ready .visualstudio:active, .siteHome .ready .aspnet:hover, .siteHome .ready .aspnet:active, .siteHome .ready .dotnet:hover, .siteHome .ready .dotnet:active {
	background-color: #5c2d91;
}

.siteHome .ready .office:hover, .siteHome .ready .office:active {
	background-color: #d83b01;
}

.siteHome .ready .dynamics:hover, .siteHome .ready .dynamics:active {
	background-color: #002050;
}

.siteHome .ready .em:hover, .siteHome .ready .em:active {
	background-color: #107c10;
}

.siteHome .ready .nuget:hover, .siteHome .ready .nuget:active {
	background-color: #164f78;
}

.hasTouch .siteHome .ready .cardImageOuter:hover, .hasTouch .siteHome .ready .cardImageOuter:active {
    background-color: #fff;
}

.siteResources {
    background-color: #3498db;    
}

.siteResources h2 {
    color: #fff;
}

.siteResources .pageFooter ul.links li a {
    color: #fff;
}

#footer {
    margin-top:0;
    border:0;
}

@media only screen and (min-width: 1281px) {
    .siteHome .cardsX > li {    
        -webkit-flex: 0 1 25%;
        -ms-flex: 0 1 25%;
        flex: 0 1 25%;
    }
}

html[lang='en-us'] .enOnly {
    display: none;
}

.enOnly {
	background-color: #1867c1;
	color: #fff;
	padding: 0 4px;
	font-size: 0.825rem;
	margin: 2px;
	position: absolute;
	top: 2px;
	left: 14px;
    margin-right:16px;
	z-index:300;
}

header >.enOnly { /* special for teamblog */
	display: inline-block;
	line-height: 1.2;	
	padding: 6px;	
	position: static;
}

.enOnlyDetail{
	border-left: solid 1px #bbb;
    display:none;
    line-height: 1.2;
	margin-left:2px;
	padding-left: 6px;
}

.enOnly:hover .enOnlyDetail {
    display:inline-block;
}

/* v1 "blue boxes" below */

html.theme_night #hero-content h2,
html.theme_night #hero-content h3,
html.theme_night h2.section-heading  {
    color:#828282;
}

