mark {
    font-size: 0.8em;
    vertical-align: baseline;
    padding: 0em 0.45em 0.1em 0.45em;
    border-radius: 2px;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(0,0,0,0.05);
    background-color: rgba(0, 0, 0, 0.05);
    color: #333;
}

.theme_night mark {
    border-color: #ccc;
    background-color: transparent;
    color: #ccc;
    margin-right: .45em;
}

/* base page update above this line */

#hero-content {
    position: relative;
    display: block;
    background-color: #f7f7f8;
    padding-top: 2.5em;
    padding-bottom: 2.5em;
    z-index: 1;
}

.theme_night #hero-content {
    background-color: #1a1a1a;
}

    #hero-content:after {
        position: absolute;
        content: "";
        background-size: 26px;
        background-image: url(../images/graph-bg.png);
        background-repeat: repeat;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        opacity: 0.06;
        z-index: -1;
    }

    #hero-content h1 {
        margin-top: 0;
        padding: 0 1em 0.2em 1em;
        font-size: 2.4em;
        text-align: center;
    }

    #hero-content h2 {
        margin: 0 auto;
        padding: 0 1em 1.5em 1em;
        font-size: 1.2em;
        line-height: 1.7em;
        text-align: center;
        max-width: 46em;
    }

    #hero-content #actions {
        text-align: center;
    }

    #hero-content h3 {
        color: #717171;
        font-size: 0.8em;
        text-align: center;
    }
    #hero-content h3 a {
        color: #0078b4;
    }

.graph#hero-content:after {
    background-image: url(../images/graph2-bg.png);
}

.theme_night .graph#hero-content:after {
    background-image: url(../images/graph2-theme_dark-bg.png);
}

.action-list {
    list-style: none;
    margin:0;
}

    .action-list li {
        display: inline-block;
        padding: 0.5em;
    }

        .action-list li:first-child {
            padding-left: 0;
        }

        .action-list li:last-child {
            padding-right: 0;
        }

    .action-list:after {
        display: table;
        content: "";
        clear: both;
    }

.alert {
    padding: 0.5em;
    text-align: center;
    background-color: #feecc5;
}

.theme_night .alert {
    background-color: #333;
}

    .alert p {
        display: inline-block;
        margin: 0;
        color: rgba(0,0,0,0.66);
    }
   
    .theme_night .alert p {
        color: #ccc;
    }

        .alert p:after {
            content: "";
            padding-left: 0.7em;
            padding-right: 0.7em;
        }

    .alert .action-list {
        display: inline-block;
    }

    .alert a.button-bordered {
        padding: 0 1em;
        font-size: 0.6em;
        border-width: 1px;
    }

.alert-social {
    padding: 0.2em;
    margin-top: 3em;
    background-color: #f7f7f8;
    border-top: 1px solid #ebebeb;
    font-size: 0.8em;
}

    .alert-social a.button-bordered {
        padding: 0 1em;
    }

#featured {
    padding: 3em 1em;
}

@media only screen and (min-width: 52.0625em) {
    #featured h2 {
        display: inline-block;
        float: left;
        width: 31.5%;
        margin-right: 1.5%;
        text-align: left;
    }
}

.features {
    padding: 1em 2em 1em 3em;
    color: #3c763d;
    background-color: #dff0d8;
    border: 1px solid #d0e9c6;
}

.theme_night .features {
    color: #ccc;
    background-color: #333;
    border: 1px solid #ccc;
}

    .features ul {
        list-style: none;
    }

    .features li {
        padding: 0.5em 0;
    }

        .features li:before {
            margin-left: -1.5em;
            padding-right: 0.5em;
            font-size: 0.8em;
            font-family: 'icons';
            content: "\e761";
        }

    .features a:link, .features a:active, .features a:visited {
        color: #3c763d;
    }

    .theme_night .features a:link, .theme_night .features a:active, .theme_night .features a:visited {
        color: #ccc;
    }

    .features a:hover, .features a:active {
        color: #112111;
    }

    .theme_night .features a:hover, .theme_night .features a:active {
        color: #eee;
    }

#journeys {
    padding: 2em 1em;
    border-top: 1px solid #ebebeb;
    background-color: #f7f7f8;
}

.theme_night #journeys {
    background-color: #333;
}

.journey-step {
    display: block;
    margin-bottom: 1em;
    padding: 1.5em;
    padding-bottom: 0;
    list-style: none;
    color: #333;
    background-color: #fdfdfd;
    border: 1px solid #f0f0f0;
}

.theme_night .journey-step {
    color: #ccc;
    background-color: #1A1A1A;
}

.journey-step-header > a {
    display: block;
    overflow: auto;
    color: #333;
}

.theme_night .journey-step-header > a {
    color: #ccc;
}

    .journey-step-header > a:hover {
        color: #006fa9;
    }

    .theme_night .journey-step-header > a:hover {
        color: #eee;
    }

        .journey-step-header > a:hover .icon {
            color: #006fa9;
        }

        .theme_night .journey-step-header > a:hover .icon {
            color: #333;
        }

.journey-step-header .icon {
    display: inline-block;
    float: left;
    margin-right: 0.5em;
    color: rgba(0,0,0,0.45);
    font-size: 1.5em;
    font-weight: 200;
    vertical-align: text-top;
}

.journey-step-header .step-number {
    display: inline-block;
    width: 2.4em;
    height: 2.4em;
    margin-right: 1em;
    float: left;
    background-color: #f7f7f8;
    border: 1px solid #ebebeb;
    border-radius: 100px;
    text-align: center;
    font-size: 0.7em;
}

.theme_night .journey-step-header .step-number {
    color: #222;
}


.journey-step-header .title {
    font-weight: bolder;
    margin-bottom: 0.5em;
}

    .journey-step-header .title p {
        margin: 0;
        display: inline;
    }

.journey-step-header .description {
    margin-top: 0;
    margin-bottom: 1.5em;
}

.journey-step-elements {
    position: relative;
}

    .journey-step-elements li {
        list-style: none;
        margin-bottom: 1.5em;
        position: relative;
    }

        .journey-step-elements li > a {
            display: block;
            overflow: auto;
            padding: 1em;
            color: #006fa9;
            background-color: #f3fbff;
            border: 1px solid #daf3ff;
        }

        .theme_night .journey-step-elements li > a {
            color: #ccc;
            background-color: #333;
            border-color: #ccc;
        }

@media only screen and (min-width: 40.0625em) {
    .journey-step-elements li > a {
        min-height: 11em;
    }
}

.journey-step-elements li > a:hover {
    background-color: #daf3ff;
    color: #003d5d;
    border: 1px solid #c0eaff;
}

.theme_night .journey-step-elements li > a:hover {
    background-color: #444;
    color: #eee;
    border: 1px solid #eee;
}

    .journey-step-elements li > a:hover h4 {
        color: #003d5d;
    }

    .theme_night .journey-step-elements li > a:hover h4 {
        color: #eee;
    }

.journey-step-elements h3 {
    margin: 0;
    font-size: 0.9em;
}

.journey-step-elements p {
    margin-bottom: 2em;
    font-size: 0.9em;
    line-height: 1.5em;
}

.journey-step-elements h4 {
    margin-bottom: 0;
    margin-top: 1.5em;
    position: absolute;
    bottom: 1.5em;
    color: #76d0ff;
    font-size: 0.66em;
    text-transform: uppercase;
    letter-spacing: 0.1em;
}

.theme_night .journey-step-elements h4 {
    color: #ccc;
}

a.see-more {
    display: block;
    margin-bottom: 2em;
    color: #006fa9;
    font-size: 0.8em;
    font-weight: bolder;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
}

    a.see-more:after {
        content: " ";
    }

    a.see-more:hover {
        color: #003d5d;
    }

.resources {
    padding: 3em 1em;
}

    .resources h2 {
        font-size: 1.2em;
        text-transform: none;
    }

.resource-list ul {
    list-style: none;
}

.resource-list li {
    padding: 0.5em 0;
    margin-left: 1.5em;
